import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Component, Inject, NgZone, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { AuthService } from "../../shared/services/auth.service";


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  d = new Date(); 
  currentYear = this.d.getFullYear(); 

  exit: boolean = false;
  router: string;

  constructor( 
    public authService: AuthService,
    private breakpointObserver: BreakpointObserver,
    public dialog: MatDialog,
    private _router: Router
    ) { 
      this.router = _router.url; 
    }

  ngOnInit(): void {
    
  }

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );

  isWebset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Web)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );

    openDialog(): void {
      const dialogRef = this.dialog.open(ExitDialog, {
        height: '180px',
        width: '400px',
      });

      dialogRef.afterClosed().subscribe(result => {
        if(result){
          this.authService.Exit();
        }
      });
  
    }
}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: './exit-dialog.component.html',
})
export class ExitDialog {

  constructor(
    public authService: AuthService,
    public dialogRef: MatDialogRef<ExitDialog>,
   ) {}

  onNoClick(): void {
    this.dialogRef.close(false);
  }
  onExitClick(): void {
    this.dialogRef.close(true);
  }
}
